import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpService } from '../../app/http.service'
declare var $:any;

@Component({
  selector: 'app-forgotpassword',
  templateUrl: './forgotpassword.component.html'
})
export class ForgotpasswordComponent implements OnInit {
	loading = false;
	sent = false;
	error: string;
	email = '';
	valid = false;
	first = true;

	constructor(
		private formBuilder: FormBuilder,
		private route: ActivatedRoute,
		private router: Router,
		private httpService: HttpService) {

	}

	ngOnInit() {
        $(document).ready(function(){
            setTimeout(function(){
                $('#server-wait').hide();
                $('#main-div').show();
            },100)
        });
    }

	email_validation(){
		var obj_this = this;
		obj_this.first = false;
		var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		if(re.test(String(obj_this.email).toLowerCase())){
			obj_this.valid = true;
		}
		else{
			obj_this.valid = false;
		}
	}

	onSubmit() {

		this.loading = true;
		let obj_this = this;

		let req_url = '/password-reset-email';
		let input_data = {
			login: this.email
		};
		var success_cb = function (result) {
			obj_this.loading = false;
			obj_this.sent = true;
		};
		var failure_cb = function (error) {
			obj_this.error = error;
			obj_this.loading = false;
		};
		this.httpService.call_post_http(req_url, input_data, success_cb, failure_cb,);
	}
}
