import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../app/http.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-votingdetails',
  templateUrl: './votingdetails.component.html'
})
export class VotingdetailsComponent implements OnInit {

  
  constructor(private httpService: HttpService,private route: ActivatedRoute) {  }
   
  get_voting_data() {
    var req_url = '/voting/details';
    let voting_id = this.route.snapshot.params.id;
    var input_data = {voting_id: voting_id};
    var obj_this = this;
    var voting_data = '';
    var success_cb = function(voting_data) {
        if (!voting_data) {
            console.log("invalid data", voting_data);
            return;
        }
        console.log(voting_data)
      }
      obj_this.httpService.call_post_http(req_url, input_data, success_cb,null);
    }
  ngOnInit() {
    var this_obj = this;
    this_obj.get_voting_data();
  }

}
