﻿import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { HttpService } from "../../app/http.service";
import { DomSanitizer } from '@angular/platform-browser';

@Component({
    templateUrl: 'commiteedetails.component.html'
})
export class CommitteeDetailsComponent implements OnInit {
    committee: any;
    img: string = 'http://pngimg.com/uploads/folder/folder_PNG8773.png';
    params: any;
    next = '';
    prev = '';

    constructor(private httpService: HttpService, private route: ActivatedRoute, private sanitizer: DomSanitizer) {
        this.route.params.subscribe(params => this.get_data());
    }

    get_data()
    {
        var obj_this = this;
        let id = obj_this.route.snapshot.params.id;
        let input_data = { id: id };        
        obj_this.httpService.call_post_http('/committee/details-json', input_data,
            (result) => {
                obj_this.committee = result.committee;
                obj_this.next = result.next;
                obj_this.prev = result.prev;
                obj_this.committee.summary = obj_this.sanitizer.bypassSecurityTrustHtml(obj_this.committee.summary);
            }, false);
    }

    next_prev(id){
        var input_data = { id: id };
        var obj_this = this;
        obj_this.httpService.call_post_http('/committee/details-json', input_data,
            (result) => {
                obj_this.committee = result.committee;
                obj_this.next = result.next;
                obj_this.prev = result.prev;
                obj_this.committee.summary = obj_this.sanitizer.bypassSecurityTrustHtml(obj_this.committee.summary);
            }, false);
    }

    ngOnInit() {
        
    }
}
