import { Injectable } from '@angular/core';
import { call_ajax } from './js_methods';

@Injectable()
export class HttpService {        
    total_records;
    server_url;    
    count;

    constructor() {
        this.server_url = window['server_url'];       
    }

    fetch_paged_data = function(off_set, limit){};

    call_post_http(url: string, input_data: any, success_cb, failure_cb) {
        var options = this.makeOptions(url,input_data,success_cb, failure_cb);        
        call_ajax(options);
    }

    call_post_http_post(url: string, input_data: any, success_cb, failure_cb, req_type) {
        var options = this.makeOptions(url,input_data,success_cb, failure_cb);
        console.log(options);
        if(req_type){
            options['type'] = req_type;
        }
        try{
			call_ajax(options);
		}catch (e) {
			console.log(e)
		}
    }

    authenticate(url: string, input_data: any, success_cb, failure_cb, complete_cb) {
        const httpservie = this;        
        var options = httpservie.makeOptions(url, input_data, success_cb, failure_cb);
        options.onSuccess = function(data){            
            window['current_user'].onLogin(data);
            if(success_cb)
            	success_cb(data);
        };
        options.type = 'get';
        options.onComplete = complete_cb;
        options.onError = failure_cb;
        call_ajax(options);
    }
    
    makeOptions(url, input_data, success_cb, failure_cb) {
        const http_service = this;
        var onRequestFailed = function(res)
        {
            if(failure_cb)
                failure_cb(res);
            else
            {
                console.log(res, url);                
            }
        };
        var options = {
            url:url,
            type: 'get',
            before:function(a, b){
                //console.log(b.url);
            },
            data:input_data,            
            //type:'post',
            onSuccess:success_cb,
            onError:onRequestFailed,
            onComplete:function(){
            }
        };
        return options;
    }

    make_bread_crumb() {
        let comeplete_url = window.location + '';        
        let base_url = window.location.origin + '';
        let page_url = comeplete_url.replace(base_url + '/', '');

        let ar = page_url.split('/');
        let last_link = '';
        let links = []
        for ( var i in ar) {
            if (parseInt(i) !== ar.length - 1) {
                last_link = last_link + '/' + ar[i];                
                links.push({url: last_link, title: ar[i]});
            }
        }
        return links;
    }
}
