﻿import { Component, OnInit } from '@angular/core';
import { HttpService } from "../../app/http.service";

@Component({
    templateUrl: 'profiles.component.html'
})
export class ProfilesComponent implements OnInit {
    profiles_data: any;
    no_prof = false;

    constructor(private httpService: HttpService) {
        const obj_this = this;
        this.profiles_data = [];
        this.get_data();
    }

    get_data(){
        var obj_this = this;
        const input_data = {
            
        }
        obj_this.httpService.call_post_http('/ws/profiles-json', input_data ,
            (result) => {                    
                if(result)
                {
                    //console.log(result.records);
                    obj_this.profiles_data = result.records;
                    obj_this.httpService.total_records = result.total;
                    obj_this.profiles_data && obj_this.profiles_data.length > 0 ? obj_this.no_prof = false : obj_this.no_prof = true;
                }                    
        }, (error) => {});
    }

    ngOnInit() {
    }
}
